<template>
  <div id="title-shower-wrapper">
    <transition
      name="out-down"
      v-on:before-enter="titleEnter"
      v-on:after-leave="titleLeave"
      appear
    >
      <div id="title" v-if="step === 0">
        <div>
          <p style="font-size: 40px;text-align: left;">大渣男</p>
          <span style="font-size: 100px" class="bolder red">红毛</span>
          <br />
          <p style="font-size: 20px;text-align: right;">↓ 滑动查看 ↓</p>
        </div>
      </div>
    </transition>
    <!---->
    <transition name="bigger">
      <div class="step-title" v-if="isNextTitleShow">
        <p style="font-size: 100px;">
          {{ desc[step - 1 === -1 ? 0 : step - 1] }}
        </p>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'TitleShower',
  props: {
    step: {
      default: 0
    }
  },
  data: function() {
    return {
      isNextTitleShow: false,
      desc: ['低能', '流氓', '不雅', '滥交']
    }
  },
  methods: {
    titleLeave() {
      this.isNextTitleShow = true
    },
    titleEnter() {
      this.isNextTitleShow = false
    }
  },
  watch: {
    step(val) {
      this.isNextTitleShow = false
      setTimeout(() => {
        this.isNextTitleShow = true
        if (val === 0) this.isNextTitleShow = false
      }, 300)
    }
  }
}
</script>

<style lang="scss" scoped>
#title-shower-wrapper {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  box-sizing: border-box;
  top: 0;
  left: 0;
  font-family: 'Adobe 黑体 Std R', sans-serif;

  #title {
    box-sizing: border-box;
    animation: in 0.5s;
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: column;

    .bolder {
      font-weight: bolder;
    }

    .red {
      color: lightcoral;
    }

    p,
    span {
      text-shadow: #000000 5px 5px 20px;
    }

    p {
      color: wheat;
    }

    @keyframes in {
      0% {
        transform: scale(3);
      }
      100% {
        transform: scale(1);
      }
    }
  }

  .step-title {
    color: indianred;
    position: fixed;
    top: 0;
    font-weight: bolder;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .out-down-enter-active,
  .out-down-leave-active {
    transition: all 0.5s;
  }

  .out-down-enter,
  .out-down-leave-to {
    opacity: 0;
    transform: translateY(100vw);
  }

  .bigger-enter-active {
    animation: bigger 0.3s;
  }

  .bigger-leave-active {
    animation: bigger 0.3s reverse;
  }

  .bigger-enter,
  .bigger-leave-to {
    opacity: 1;
  }

  @keyframes bigger {
    0% {
      transform: scale(3);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
}
</style>
